import React, { Component, createRef } from "react";
import Sidebar from "../Sidebar/Sidebar";
import Panel from "../Panel/Panel";
import Board from "../Board/Board";
import * as d3 from "d3";
export default class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: { line: [], scatter: [], area: [] },
      x: { line: [], scatter: [], area: [] },
      y: { line: [], scatter: [], area: [] },
      xData: { line: [], scatter: [], area: [] },
      yData: { line: [], scatter: [], area: [] },
      xDatas: [],
      yDatas: [],
      color: { line: [], scatter: [], area: [] },
      graphMode: "line",
      graphModes: [],
      graphName: { line: [], scatter: [], area: [] },
    };
    this.getXY = this.getXY.bind(this);
    this.boardRef = createRef();
    this.removeId = this.removeId.bind(this);
    this.updateGraphMode = this.updateGraphMode.bind(this);
  }

  updateGraphMode(mode) {
    this.setState({
      graphMode: mode,
    });
  }

  getXY(id, x, y, xData, yData, dx, dy, color, graphMode, graphName) {
    console.log(id, x, y, xData, yData, dx, dy, color, graphMode, graphName);
    let p = new Promise((resolve, reject) => {
      let dx = [];
      let dy = [];
      let updateIdx = -1;
      let newXData = this.state.xData;
      let newYData = this.state.yData;
      let c = this.state.color;
      let gM = this.state.graphModes;
      let n = this.state.graphName;
      let newX = this.state.x;
      let newY = this.state.y;

      if (this.state.id[graphMode].includes(id[graphMode])) {
        updateIdx = this.state.id[graphMode].indexOf(id[graphMode]);

        newXData[graphMode][updateIdx] = xData[graphMode];
        newYData[graphMode][updateIdx] = yData[graphMode];
        c[graphMode][updateIdx] = color[graphMode];
        gM[updateIdx] = graphMode;
        n[graphMode][updateIdx] = graphName[graphMode];
        newX[graphMode][updateIdx] = x[graphMode];
        newY[graphMode][updateIdx] = y[graphMode];
        console.log(newXData);
        Object.keys(newXData).forEach((e, i) => {
          newXData[e].forEach((el, index) => {
            dx = [...dx, ...el];
            dy = [...dy, ...newYData[e][index]];
          });
        });
      }
      resolve([newXData, newYData, updateIdx, dx, dy, c, gM, n, newX, newY]);
    });
    p.then((d) => {
      if (d[2] === -1)
        this.setState((prevState) => ({
          id: {
            ...prevState.id,
            [graphMode]: [...prevState.id[graphMode], id[graphMode]],
          },
          x: {
            ...prevState.x,
            [graphMode]: [...prevState.x[graphMode], x[graphMode]],
          },
          y: {
            ...prevState.y,
            [graphMode]: [...prevState.y[graphMode], y[graphMode]],
          },
          xData: {
            ...prevState.xData,
            [graphMode]: [...prevState.xData[graphMode], xData[graphMode]],
          },
          yData: {
            ...prevState.yData,
            [graphMode]: [...prevState.yData[graphMode], yData[graphMode]],
          },
          xDatas: dx,
          yDatas: dy,
          color: {
            ...prevState.color,
            [graphMode]: [...prevState.color[graphMode], color[graphMode]],
          },
          graphModes: [...prevState.graphModes, graphMode],
          graphName: {
            ...prevState.graphName,
            [graphMode]: [
              ...prevState.graphName[graphMode],
              graphName[graphMode],
            ],
          },
        }));
      else {
        this.setState((prevState) => ({
          id: this.state.id,
          x: d[8],
          y: d[9],
          xData: d[0],
          yData: d[1],
          xDatas: d[3],
          yDatas: d[4],
          color: d[5],
          graphModes: d[6],
          graphName: d[7],
        }));
      }
      // setTimeout(() => console.log(this.state), 2000);
    });
  }

  removeId(i, graphMode) {
    console.log(i);
    let idx = this.state.id[graphMode].indexOf(i);
    let { x, y, xData, yData, id, color, graphName } = this.state;
    x[graphMode].splice(idx, 1);
    y[graphMode].splice(idx, 1);
    xData[graphMode].splice(idx, 1);
    yData[graphMode].splice(idx, 1);
    color[graphMode].splice(idx, 1);
    id[graphMode].splice(idx, 1);
    graphName[graphMode].splice(idx, 1);
    let dx = [];
    let dy = [];
    Object.keys(xData).forEach((e) => {
      xData[e].forEach((el, index) => {
        dx = [...dx, ...el];
        dy = [...dy, ...yData[e][index]];
      });
    });
    this.setState({
      x: x,
      y: y,
      xData: xData,
      yData: yData,
      id: id,
      xDatas: dx,
      yDatas: dy,
      color: color,
      graphName: graphName,
    });
    // setTimeout(() => console.log(this.state), 2000);

    // this.boardRef.current.removeId(id);
  }

  render() {
    return (
      <>
        <Sidebar updateGraphMode={this.updateGraphMode}></Sidebar>
        <div className="main">
          {this.state.graphMode == "area" ? (
            <Panel
              getXY={this.getXY}
              remove={this.removeId}
              graphMode={this.state.graphMode}
            ></Panel>
          ) : this.state.graphMode == "scatter" ? (
            <Panel
              getXY={this.getXY}
              remove={this.removeId}
              graphMode={this.state.graphMode}
            ></Panel>
          ) : this.state.graphMode == "line" ? (
            <Panel
              getXY={this.getXY}
              remove={this.removeId}
              graphMode={this.state.graphMode}
            ></Panel>
          ) : null}
          <Board
            ref={this.boardRef}
            id={this.state.id}
            x={this.state.x}
            y={this.state.y}
            xData={this.state.xData}
            yData={this.state.yData}
            xDatas={this.state.xDatas}
            yDatas={this.state.yDatas}
            color={this.state.color}
            graphModes={this.state.graphModes}
            graphName={this.state.graphName}
          ></Board>
        </div>
      </>
    );
  }
}
