import React, { Component, createRef } from "react";
import Canvas from "./Canvas";
import * as d3 from "d3";
import canvg from "canvg";
import html2canvas from "html2canvas";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowCircleDown } from "@fortawesome/free-solid-svg-icons";

export default class Board extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: null,
      x: null,
      y: null,
      xData: null,
      yData: null,
      xDatas: [],
      yDatas: null,
      xScale: null,
      yScale: null,
      svg: null,
      xAxis: null,
      yAxis: null,
      width: null,
      height: null,
      g: null,
      update: [],
      color: null,
      graphModes: null,
      graphName: null,
    };
    this.canvasRef = createRef();
    this.line = createRef();
    // this.removeId = this.removeId.bind(this);
    this.downloadPng = this.downloadPng.bind(this);
    this.downloadSvg = this.downloadSvg.bind(this);
  }

  componentWillReceiveProps(nextProps) {
    this.setState({
      id: nextProps.id,
      x: nextProps.x,
      y: nextProps.y,
      xData: nextProps.xData,
      yData: nextProps.yData,
      xDatas: nextProps.xDatas,
      yDatas: nextProps.yDatas,
      color: nextProps.color,
      graphModes: nextProps.graphModes,
      graphName: nextProps.graphName,
      update: [...this.state.update, Math.random()],
    });
  }

  componentDidMount() {
    this.drawCanvas();
  }

  // removeId(i) {
  //   let idx = this.state.id.indexOf(i);
  //   let { xData, yData, id, color, graphName } = this.state;
  //   xData.splice(idx, 1);
  //   yData.splice(idx, 1);
  //   color.splice(idx, 1);
  //   id.splice(idx, 1);
  //   graphName.splice(idx, 1);
  //   let dx = [];
  //   let dy = [];
  //   xData.forEach((e, i) => {
  //     dx = [...dx, ...e];
  //     dy = [...dy, ...yData[i]];
  //   });
  //   this.setState({
  //     xData: xData,
  //     yData: yData,
  //     id: id,
  //     xDatas: dx,
  //     yDatas: dy,
  //     color: color,
  //     graphName: graphName,
  //   });
  // }

  drawCanvas() {
    var margin = { top: 50, right: 50, bottom: 50, left: 50 },
      width = this.canvasRef.current.clientWidth - margin.left - margin.right,
      height = this.canvasRef.current.clientHeight - margin.top - margin.bottom;

    let svg = d3
      .select(this.canvasRef.current)
      .append("svg")
      .attr("width", width + margin.left + margin.right)
      .attr("height", height + margin.top + margin.bottom)
      .append("g")
      .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

    const g1 = svg.append("g");
    const g2 = svg.append("g");

    var xScale = d3.scaleLinear().range([0, width - 100]);
    var yScale = d3.scaleLinear().range([height, 0]);

    let xAxis = d3.axisBottom(xScale);
    let yAxis = d3.axisLeft(yScale);

    this.setState({
      xScale: xScale,
      yScale: yScale,
      svg: svg,
      xAxis: xAxis,
      yAxis: yAxis,
      width: width,
      height: height,
      g: { g1: g1, g2: g2 },
    });
  }

  parseData(x, y) {
    let lineData = [];
    let scatterData = [];
    x["line"].forEach((e, i) => {
      lineData.push([e, y["line"][i]]);
    });
    x["scatter"].forEach((e, i) => {
      scatterData.push([e, y["scatter"][i]]);
    });
    return { line: lineData, scatter: scatterData };
  }

  downloadPng = () => {
    html2canvas(this.canvasRef.current).then((d) => {
      let url = d.toDataURL("image/png");
      let link = document.createElement("a");

      link.href = url;
      link.download = "chart.png";
      document.body.appendChild(link);
      link.click();
    });
  };

  downloadSvg = () => {
    d3.select(this.canvasRef.current)
      .append("a")
      .attr("href-lang", "image/svg+xml")
      .attr(
        "href",
        'data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg">' +
          unescape(
            d3.select(this.canvasRef.current).select("svg").node().parentNode
              .innerHTML
          ) +
          "</svg>"
      )
      .text("Download");
  };

  render() {
    return (
      <div className="board">
        <div ref={this.canvasRef} className="canvas" id="canvas">
          {this.state.height &&
          this.state.xData &&
          this.state.xDatas &&
          this.state.update ? (
            <Canvas
              ref={this.line}
              id={this.state.id}
              svg={this.state.svg}
              xData={this.state.xData}
              yData={this.state.yData}
              xScale={this.state.xScale}
              yScale={this.state.yScale}
              xDatas={this.state.xDatas}
              yDatas={this.state.yDatas}
              xAxis={this.state.xAxis}
              yAxis={this.state.yAxis}
              width={this.state.width}
              height={this.state.height}
              g={this.state.g}
              color={this.state.color}
              graphModes={this.state.graphModes}
              graphName={this.state.graphName}
              update={this.state.update}
            ></Canvas>
          ) : null}
        </div>
        <div>
          <span
            style={{
              width: "10rem",
              padding: "2rem",
              fontSize: "0.8rem",
            }}
          >
            Made by Varun A V
          </span>
          {this.state.height &&
          this.state.xData &&
          this.state.xDatas.length != 0 &&
          this.state.update ? (
            <div style={{ float: "right" }}>
              <button className="downloadButton" onClick={this.downloadPng}>
                <span
                  style={{
                    paddingRight: "1rem",
                    fontSize: "1.1rem",
                  }}
                >
                  PNG
                </span>
                <FontAwesomeIcon
                  icon={faArrowCircleDown}
                  size="3x"
                  color="black"
                />
              </button>
            </div>
          ) : null}
        </div>
      </div>
    );
  }
}
