import React, { Component } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faCoffee,
  faChartLine,
  faChartBar,
  faChessBoard,
  faChartArea,
  faCircle,
  faAdjust,
  faMoon,
  faSun,
  faBraille,
} from "@fortawesome/free-solid-svg-icons";
import fav from "./fav1.png";

export default class Sidebar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      graphMode: "line",
      lineActive: "active",
      barActive: "",
      areaActive: "",
      scatterActive: "",
      theme: "light",
    };
  }

  changeTheme = (event) => {
    if (this.state.theme === "light") {
      document.documentElement.style.setProperty("--text-primary", "#b6b6b6");
      document.documentElement.style.setProperty("--text-secondary", "#ececec");
      document.documentElement.style.setProperty("--bg-primary", "#23232e");
      document.documentElement.style.setProperty("--bg-secondary", "#141418");
    } else if (this.state.theme === "dark") {
      document.documentElement.style.setProperty("--text-primary", "#1f1f1f");
      document.documentElement.style.setProperty("--text-secondary", "#000000");
      document.documentElement.style.setProperty("--bg-primary", "#ffffff");
      document.documentElement.style.setProperty("--bg-secondary", "#e4e4e4");
    }
    this.setState({ theme: this.state.theme == "light" ? "dark" : "light" });
  };

  graphChange = (event) => {
    if (event.target.id === "lineGraph") {
      this.setState({
        graphMode: "line",
        lineActive: "active",
        barActive: "",
        areaActive: "",
        scatterActive: "",
      });
      this.props.updateGraphMode("line");
    } else if (event.target.id === "barGraph") {
      this.setState({
        graphMode: "bar",
        lineActive: "",
        barActive: "active",
        areaActive: "",
        scatterActive: "",
      });
      this.props.updateGraphMode("bar");
    } else if (event.target.id === "areaGraph") {
      this.setState({
        graphMode: "area",
        lineActive: "",
        barActive: "",
        areaActive: "active",
        scatterActive: "",
      });
      this.props.updateGraphMode("area");
    } else if (event.target.id == "scatterGraph") {
      this.setState({
        graphMode: "scatter",
        lineActive: "",
        barActive: "",
        areaActive: "",
        scatterActive: "active",
      });
      this.props.updateGraphMode("scatter");
    }
  };

  render() {
    return (
      <nav className="navbar">
        <ul className="navbar-nav">
          <li className="nav-item brand">
            <a href="#" className="nav-link">
              {/* <FontAwesomeIcon
                icon={faChessBoard}
                size="3x"
                color="rgb(34, 128, 206)"
              /> */}
              <img style={{ marginLeft: "1.5rem" }} src={fav}></img>
              <span className="link-text">Varun</span>
            </a>
          </li>
          <hr></hr>
          <li className={"nav-item " + this.state.lineActive}>
            <div className="nav-link" onClick={this.graphChange} id="lineGraph">
              <FontAwesomeIcon
                icon={faChartLine}
                size="lg"
                color="orange"
                id="lineGraph"
                style={{ "pointer-events": "none" }}
              />
              <span
                className="link-text"
                id="lineGraph"
                style={{ "pointer-events": "none" }}
              >
                Line plot
              </span>
            </div>
          </li>
          <li className={"nav-item " + this.state.scatterActive}>
            <div
              className="nav-link"
              onClick={this.graphChange}
              id="scatterGraph"
            >
              <FontAwesomeIcon
                icon={faBraille}
                size="2x"
                color="orange"
                id="scatterGraph"
                style={{ "pointer-events": "none" }}
              />

              <span
                className="link-text"
                id="ScatterGraph"
                style={{ "pointer-events": "none" }}
              >
                Scatter plot
              </span>
            </div>
          </li>
          {/* <li className={"nav-item " + this.state.barActive}>
            <div className="nav-link" onClick={this.graphChange} id="barGraph">
              <FontAwesomeIcon
                icon={faChartBar}
                size="lg"
                color="orange"
                id="barGraph"
              />

              <span className="link-text" id="barGraph">
                Bar chart
              </span>
            </div>
          </li> */}
          <li className={"nav-item " + this.state.areaActive}>
            <div className="nav-link" onClick={this.graphChange} id="areaGraph">
              <FontAwesomeIcon
                icon={faChartArea}
                size="lg"
                color="orange"
                id="areaGraph"
                style={{ "pointer-events": "none" }}
              />

              <span
                className="link-text"
                id="areaGraph"
                style={{ "pointer-events": "none" }}
              >
                Area chart
              </span>
            </div>
          </li>
          <li className="nav-item">
            <div className="nav-link" onClick={this.changeTheme} id="theme">
              {this.state.theme === "light" ? (
                <FontAwesomeIcon
                  icon={faSun}
                  size="lg"
                  color="orange"
                  id="theme"
                  style={{ "pointer-events": "none" }}
                />
              ) : (
                <FontAwesomeIcon
                  icon={faMoon}
                  size="lg"
                  id="theme"
                  style={{ "pointer-events": "none" }}
                />
              )}
              <span
                className="link-text"
                id="theme"
                style={{ "pointer-events": "none" }}
              >
                Change theme
              </span>
            </div>
          </li>
        </ul>
      </nav>
    );
  }
}
