import React, { Component, createRef } from "react";
import * as d3 from "d3";
import { pointRadial, line } from "d3";

export default class Canvas extends Component {
  constructor(props) {
    super(props);
    this.state = {
      svg: this.props.svg,
    };
    this.drawLine = this.drawLine.bind(this);
    // this.removeId = this.removeId.bind(this);
  }
  componentDidMount() {
    this.draw();
  }

  componentDidUpdate() {
    this.draw();
    console.log("updaing canvas...");
  }

  draw() {
    if (this.props.xDatas.length == 0) {
      this.props.svg.classed("svgNoDisplay", true);
    } else {
      this.props.svg.classed("svgNoDisplay", false);
    }

    this.axisUpdate();

    const data = this.parseData(this.props.xData, this.props.yData);

    this.drawLine(data.line);
    this.drawArea(data.area);
    this.drawScatter(data.scatter);

    this.drawLegend(data.legend);
  }

  // removeId(id) {
  //   console.log(id);
  //   this.props.svg.selectAll(".line").remove();
  // }

  drawLine(data) {
    console.log(data);
    let line = d3
      .line()
      .x((d) => {
        return this.props.xScale(d[0]);
      })
      .y((d) => {
        return this.props.yScale(d[1]);
      });

    const lines = this.props.svg
      .selectAll(".line")
      .data(data)
      .attr("class", "line");

    lines.exit().remove();
    lines
      .enter()
      .append("path")
      .attr("class", "line")
      .attr("id", (d, i) => {
        return this.props.id["line"][i];
      })
      .attr("d", line)
      .attr("fill", "none")
      .attr("stroke", (d, i) => this.props.color["line"][i])
      .attr("stroke-width", "1");

    lines
      .transition()
      .duration(500)
      .attr("stroke", (d, i) => this.props.color["line"][i])
      .attr("d", line);
  }

  drawScatter(data) {
    console.log(data);
    const scatterGroup = this.props.svg.selectAll(".scatterGroup").data(data);

    scatterGroup.exit().remove();

    const scatterGroupNew = scatterGroup
      .enter()
      .append("g")
      .attr("class", "scatterGroup")
      .attr("fill", (d, i) => this.props.color["scatter"][i])
      .attr("stroke", (d, i) => this.props.color["scatter"][i]);

    const scatterPoints = scatterGroup
      .merge(scatterGroupNew)
      .selectAll(".scatterPoint")
      .data((d) => d);

    const scatterPointsNew = scatterPoints
      .enter()
      .append("circle")
      .attr("class", "scatterPoint")
      .attr("r", 5);

    scatterPoints
      .merge(scatterPointsNew)
      .transition()
      .duration(500)
      .attr("cx", (d, i) => this.props.xScale(d[0]))
      .attr("cy", (d, i) => this.props.yScale(d[1]));

    scatterGroup
      .transition()
      .duration(500)
      .attr("fill", (d, i) => this.props.color["scatter"][i])
      .attr("stroke", (d, i) => this.props.color["scatter"][i]);
  }

  drawArea(data) {
    let area = d3
      .area()
      .x((d) => this.props.xScale(d[0]))
      .y0(this.props.height)
      .y1((d) => this.props.yScale(d[1]));

    const areas = this.props.svg
      .selectAll(".area")
      .data(data)
      .attr("class", "area");

    areas.exit().remove();
    areas
      .enter()
      .append("path")
      .attr("class", "area")
      .attr("id", (d, i) => {
        return this.props.id["area"][i];
      })
      .attr("d", area)
      .attr("fill", (d, i) => this.props.color["area"][i])
      .attr("stroke", (d, i) => this.props.color["area"][i])
      .attr("stroke-width", "1");

    areas
      .transition()
      .duration(500)
      .attr("stroke", (d, i) => this.props.color["area"][i])
      .attr("fill", (d, i) => this.props.color["area"][i])
      .attr("d", area);
  }

  axisUpdate() {
    this.props.xScale.domain(d3.extent(this.props.xDatas));

    const xAxis = d3.axisBottom().scale(this.props.xScale);

    if (this.props.svg.selectAll(".x.axis").empty()) {
      this.props.g.g1
        .attr("class", "x axis")
        .attr("transform", "translate(0," + this.props.height + ")")
        .call(xAxis);
    } else {
      this.props.svg
        .selectAll(".x.axis")
        .transition()
        .duration(500)
        .call(xAxis);
    }

    this.props.yScale.domain(d3.extent(this.props.yDatas));

    const yAxis = d3.axisLeft().scale(this.props.yScale);

    if (this.props.svg.selectAll(".y.axis").empty()) {
      this.props.g.g2.attr("class", "y axis").call(yAxis);
    } else {
      this.props.svg
        .selectAll(".y.axis")
        .transition()
        .duration(500)
        .call(yAxis);
    }
  }

  drawLegend(data) {
    let customSymbolSquare = {
      draw: function (context, size) {
        let s = Math.sqrt(size) / 2;
        context.moveTo(s, s - Math.sqrt(size) / 2);
        context.lineTo(-s, s - Math.sqrt(size) / 2);
        context.lineTo(-s, s - Math.sqrt(size) / 2 - 1);
        context.lineTo(s, s - Math.sqrt(size) / 2 - 1);
        context.closePath();
      },
    };
    this.props.svg.selectAll(".symbol").remove();
    this.props.svg.selectAll(".label").remove();
    let line = d3
      .line()
      .x(this.props.width - 60)
      .y((d, i) => i * 20 + 50);

    let legendSymbol = this.props.svg.selectAll(".symbol").data(data);

    legendSymbol.exit().remove();
    legendSymbol
      .enter()
      .append("path")
      .attr("class", "symbol")
      .attr(
        "transform",
        (d, i) =>
          "translate(" + (this.props.width - 60) + "," + (i * 20 + 50) + ")"
      )
      .attr(
        "d",
        d3
          .symbol()
          .type((d, i) => {
            if (d.type === "line") {
              return customSymbolSquare;
            } else if (d.type === "scatter") {
              return d3.symbolCircle;
            } else if (d.type === "area") {
              return d3.symbolSquare;
            }
          })
          .size(100)
      )
      .style("fill", (d) => {
        return d.color;
      });

    legendSymbol
      .transition()
      .duration(100)
      .attr(
        "d",
        d3
          .symbol()
          .type((d, i) => {
            if (d.type === "line") {
              return customSymbolSquare;
            } else if (d.type === "scatter") {
              return d3.symbolCircle;
            } else if (d.type === "area") {
              return d3.symbolSquare;
            }
          })
          .size(100)
      )
      .style("fill", (d) => {
        return d.color;
      });

    let legendLabel = this.props.svg.selectAll(".label").data(data);
    legendLabel.exit().remove();
    let legendEnter = legendLabel
      .enter()
      .append("text")
      .attr("class", "label")
      .attr("x", this.props.width - 50)
      .attr("y", (d, i) => {
        return i * 20 + 55;
      })
      .text((d) => {
        return d.name;
      })
      .style("fill", (d) => {
        return d.color;
      });

    legendLabel
      .transition()
      .duration(100)
      .attr("y", (d, i) => {
        return i * 20 + 55;
      })
      .text((d) => {
        return d.name;
      })
      .style("fill", (d) => {
        return d.color;
      });
  }

  parseData(x, y) {
    let d = [];
    let lineData = [];
    let areaData = [];
    let scatterData = [];
    let legendData = [];
    x["line"].forEach((e, i) => {
      e.forEach((el, ie) => {
        d.push([el, y["line"][i][ie]]);
      });
      lineData.push(d);
      d = [];
    });
    x["area"].forEach((e, i) => {
      e.forEach((el, ie) => {
        d.push([el, y["area"][i][ie]]);
      });
      areaData.push(d);
      d = [];
    });
    x["scatter"].forEach((e, i) => {
      e.forEach((el, ie) => {
        d.push([el, y["scatter"][i][ie]]);
      });
      scatterData.push(d);
      d = [];
    });
    ["line", "scatter", "area"].forEach((e, i) => {
      this.props.graphName[e].forEach((el, ind) => {
        legendData.push({
          type: e,
          name: el,
          color: this.props.color[e][ind],
        });
      });
    });
    console.log(legendData);

    return {
      line: lineData,
      scatter: scatterData,
      area: areaData,
      legend: legendData,
    };
  }

  render() {
    return null;
  }
}
