import React, { Component } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUpload, faPlusCircle } from "@fortawesome/free-solid-svg-icons";
import { readString } from "react-papaparse";
import PanelItem from "./PanelItem";
import { ribbon } from "d3";

export default class Panel extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: {},
      nGraphs: { line: [], scatter: [], area: [] },
      xData: { line: [], scatter: [], area: [] },
      yData: { line: [], scatter: [], area: [] },
      dx: [],
      dy: [],
      color: { line: [], scatter: [], area: [] },
      graphName: { line: [], scatter: [], area: [] },
      graphModes: [],
      graphMode: "line",
    };
    this.handleData = this.handleData.bind(this);
    this.addGraph = this.addGraph.bind(this);
    this.getXY = this.getXY.bind(this);
    this.remove = this.remove.bind(this);
  }

  componentWillReceiveProps(nextProps) {
    this.setState({
      graphMode: nextProps.graphMode,
    });
    setTimeout(() => console.log(this.state.graphMode), 2000);
  }

  handleData(event) {
    this.readFileContent(event.target.files[0])
      .then((content) => {
        let d = readString(content);
        let parsedData = this.parseCSV(d.data);
        this.setState((prevData) => ({
          data: { ...prevData.data, ...parsedData },
        }));
        // alert("Uploaded data");
      })
      .catch((err) => console.log(err));
  }

  readFileContent(file) {
    const reader = new FileReader();
    return new Promise((resolve, reject) => {
      reader.onload = (event) => resolve(event.target.result);
      reader.onerror = (error) => reject(error);
      reader.readAsText(file);
    });
  }

  parseCSV(data) {
    let headers = data[0];
    let dataDict = {};
    headers.forEach((e, i) => {
      dataDict[e] = [];
      data.slice(1, data.length - 1).forEach((d) => {
        dataDict[e].push(d[i]);
      });
    });
    return dataDict;
  }

  addGraph() {
    console.log(this.state.nGraphs[this.state.graphMode]);
    if (Object.keys(this.state.data).length !== 0)
      this.setState((prevData) => ({
        nGraphs: {
          ...prevData.nGraphs,
          [this.state.graphMode]: [
            ...prevData.nGraphs[this.state.graphMode],
            {
              graph: this.state.data,
              id: "id" + Math.floor(Math.random() * 20000),
              graphMode: this.state.graphMode,
              graphData: {},
              xData: [],
              yData: [],
            },
          ],
        },
      }));
    else alert("Please upload a CSV file");
  }

  getXY(id, x, y, xData, yData, color, graphMode, graphName) {
    console.log(id, x, y, xData, yData, color, graphMode, graphName);
    let p = new Promise((resolve, reject) => {
      let dx = [];
      let dy = [];
      dx = [...this.state.dx, ...xData[this.state.graphMode]];
      dy = [...this.state.dy, ...yData[this.state.graphMode]];
      resolve([dx, dy]);
    });
    p.then((d) => {
      this.props.getXY(
        id,
        x,
        y,
        xData,
        yData,
        d[0],
        d[1],
        color,
        graphMode,
        graphName
      );

      let ng = this.state.nGraphs;
      ng[this.state.graphMode].forEach((e, i) => {
        if (e.id == id[this.state.graphMode]) {
          e.xData = xData[this.state.graphMode];
          e.yData = yData[this.state.graphMode];
          e.graphData = {
            id: id[this.state.graphMode],
            x: x[this.state.graphMode],
            y: y[this.state.graphMode],
            color: color[this.state.graphMode],
            graphName: graphName[this.state.graphMode],
          };
        }
      });
      console.log("ng", ng);
      this.setState((prevState) => ({
        nGraphs: ng,
        xData: {
          ...prevState.xData,
          [this.state.graphMode]: [
            ...prevState.xData[this.state.graphMode],
            xData[this.state.graphMode],
          ],
        },
        yData: {
          ...prevState.yData,
          [this.state.graphMode]: [
            ...prevState.yData[this.state.graphMode],
            yData[this.state.graphMode],
          ],
        },
        dx: d[0],
        dy: d[1],
        color: {
          ...prevState.color,
          [this.state.graphMode]: [
            ...prevState.color[this.state.graphMode],
            color[this.state.graphMode],
          ],
        },
        graphModes: [...prevState.graphModes, graphMode],
        graphName: {
          ...prevState.graphName,
          [this.state.graphMode]: [
            ...prevState.graphName[this.state.graphMode],
            graphName[this.state.graphMode],
          ],
        },
      }));
    });
    setTimeout(() => console.log("panel", this.state.nGraphs), 2000);
  }

  remove(id, x, y, xDat, name, graphMode) {
    console.log(x, y, xDat, graphMode);
    if (x && y && xDat.length != 0) {
      this.props.remove(id, graphMode);
      let idx = -1;
      let dx = [];
      let dy = [];
      this.state.nGraphs[this.state.graphMode].forEach((e, i) => {
        if (e.id === id) idx = i;
        else {
          if (this.state.xData[i]) {
            dx = [...dx, ...this.state.xData[i]];
            dy = [...dy, ...this.state.yData[i]];
          }
        }
      });
      let nG = this.state.nGraphs;
      let xD = this.state.xData;
      let yD = this.state.yData;
      let c = this.state.color;
      let gM = this.state.graphModes;
      let n = this.state.graphName;
      nG[this.state.graphMode].splice(idx, 1);
      xD[this.state.graphMode].splice(idx, 1);
      yD[this.state.graphMode].splice(idx, 1);
      c[this.state.graphMode].splice(idx, 1);
      gM.splice(idx, 1);
      n[this.state.graphMode].splice(idx, 1);
      this.setState({
        nGraphs: nG,
        xData: xD,
        yData: yD,
        dx: dx,
        dy: dy,
        color: c,
        graphModes: gM,
        graphName: n,
      });
    } else {
      let idx = -1;
      this.state.nGraphs[this.state.graphMode].forEach((e, i) => {
        if (e.id === id) idx = i;
      });
      let nG = this.state.nGraphs;
      nG[this.state.graphMode].splice(idx, 1);
      this.setState({
        nGraphs: nG,
      });
    }
  }

  render() {
    return (
      <div className="panel">
        <div className="upload">
          <input
            type="file"
            id="BtnBrowseHidden"
            name="file"
            accept=".csv"
            style={{ display: "none" }}
            onChange={this.handleData}
          />
          <label className="buttonUpload" for="BtnBrowseHidden" id="LblBrowse">
            <FontAwesomeIcon
              icon={faUpload}
              size="lg"
              style={{ paddingRight: "1rem" }}
            />
            Upload File
          </label>
        </div>
        <div className="addGraph" onClick={this.addGraph}>
          <span style={{ padding: "1rem" }}>Create graph </span>
          <div style={{ padding: "1rem" }}>
            <FontAwesomeIcon icon={faPlusCircle} size="2x" color="orange" />
          </div>
        </div>
        <div className="graphPanel">
          {this.state.nGraphs[this.state.graphMode].map((e, i) =>
            Object.keys(e).length !== 0 && e && this.state.data ? (
              <PanelItem
                key={e.id}
                getXY={this.getXY}
                id={e.id}
                data={this.state.data}
                remove={this.remove}
                graphMode={e.graphMode}
                graphData={e.graphData}
                xData={e.xData}
                yData={e.yData}
                defaultName={this.state.graphMode + (i + 1)}
              ></PanelItem>
            ) : null
          )}
        </div>
      </div>
    );
  }
}
