import React, { Component } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faMinus, faCross, faTimes } from "@fortawesome/free-solid-svg-icons";
import { SketchPicker, ChromePicker, CirclePicker } from "react-color";

export default class PanelItem extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: this.props.data,
      id: this.props.id,
      x: this.props.graphData.x,
      y: this.props.graphData.y,
      xData: this.props.xData,
      yData: this.props.yData,
      name: "",
      displayColorPicker: false,
      color: this.props.graphData.color
        ? this.props.graphData.color
        : "#f44336",
      graphMode: this.props.graphMode,
      graphName: this.props.graphName
        ? this.props.graphName
        : this.props.defaultName,
      graphData: this.props.graphData,
    };
    this.remove = this.remove.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.submit = this.submit.bind(this);
  }

  componentWillReceiveProps(nextProps) {
    this.setState({
      data: nextProps.data,
      id: nextProps.id,
      graphMode: nextProps.graphMode,
      graphData: nextProps.graphData,
      xData: nextProps.xData,
      yData: nextProps.yData,
      x: nextProps.graphData.x,
      y: nextProps.graphData.y,
    });
    // setTimeout(() => console.log(this.state), 2000);
  }

  remove() {
    this.props.remove(
      this.state.id,
      this.state.x,
      this.state.y,
      this.state.xData,
      this.state.graphName,
      this.state.graphMode
    );
  }

  handleClick = () => {
    this.setState({ displayColorPicker: !this.state.displayColorPicker });
  };

  handleClose = () => {
    this.setState({ displayColorPicker: false });
  };

  handleChangeComplete = (color) => {
    console.log(color);
    this.setState({ color: color.hex, displayColorPicker: false });
  };

  handleChange(event) {
    this.setState({
      [event.target.name]: event.target.value,
    });
  }

  submit(event) {
    console.log("submit", this.state.x);
    this.setState({
      xData: { [this.state.graphMode]: this.state.data[this.state.x] },
      yData: { [this.state.graphMode]: this.state.data[this.state.y] },
    });
    this.props.getXY(
      { [this.state.graphMode]: this.state.id },
      { [this.state.graphMode]: this.state.x },
      { [this.state.graphMode]: this.state.y },
      { [this.state.graphMode]: this.state.data[this.state.x] },
      { [this.state.graphMode]: this.state.data[this.state.y] },
      { [this.state.graphMode]: this.state.color },
      this.state.graphMode,
      { [this.state.graphMode]: this.state.graphName }
    );
  }

  render() {
    return (
      <>
        {this.state.data ? (
          <div className="panelItem">
            <button
              style={{
                borderRadius: "50%",
                background: `${this.state.color}`,
                float: "left",
                height: "1.5rem",
                width: "1.5rem",
              }}
              onClick={this.handleClick}
            >
              <span style={{ display: "none" }}>T</span>
            </button>
            {this.state.displayColorPicker ? (
              <div
                style={{
                  position: "absolute",
                  zIndex: "5",
                  backgroundColor: "rgba(0, 0, 0, 0.8)",
                  padding: "2rem",
                  border: "0.3rem solid black",
                  borderRadius: "1rem",
                  display: "block",
                  justifyContent: "center",
                  alignItems: "center",
                  textAlign: "center",
                }}
              >
                <div
                  style={{
                    position: "absolute",
                  }}
                  onClick={this.handleClose}
                />
                <CirclePicker
                  width={195}
                  circleSize={20}
                  circleSpacing={12}
                  color={
                    this.state.graphData.color
                      ? this.state.graphData.color
                      : this.state.color
                  }
                  onChangeComplete={this.handleChangeComplete}
                />
              </div>
            ) : null}
            <FontAwesomeIcon
              icon={faTimes}
              size="lg"
              color="orange"
              style={{ float: "right", height: "1rem", cursor: "pointer" }}
              onClick={this.remove}
            />
            <div style={{ float: "none", width: "16rem" }}>
              <input
                className="panelName"
                type="text"
                name="graphName"
                maxLength="15"
                placeholder={
                  this.state.graphData.graphName
                    ? this.state.graphData.graphName
                    : this.props.defaultName
                }
                defaultValue={
                  this.state.graphData.graphName
                    ? this.state.graphData.graphName
                    : this.props.defaultName
                }
                onChange={this.handleChange}
              />
            </div>
            <div style={{ display: "inline-block", width: "16rem" }}>
              <span style={{ fontSize: "0.8rem" }}>X : </span>
              <select onChange={this.handleChange} name="x">
                <option value="">Select Column</option>
                {this.state.data
                  ? Object.keys(this.state.data).map((e, i) =>
                      e ? (
                        <option
                          className="headersItem"
                          key={this.state.id + "x" + i}
                          value={e}
                          selected={
                            this.state.graphData.x == e ? "selected" : ""
                          }
                        >
                          {e}
                        </option>
                      ) : null
                    )
                  : null}
              </select>
            </div>
            <div>
              <span style={{ fontSize: "0.8rem" }}>Y : </span>
              <select onChange={this.handleChange} name="y">
                <option value="">Select Column</option>
                {this.state.data
                  ? Object.keys(this.state.data).map((e, i) =>
                      e ? (
                        <option
                          className="headersItem"
                          key={this.state.id + "y" + i}
                          value={e}
                          selected={
                            this.state.graphData.y == e ? "selected" : ""
                          }
                        >
                          {e}
                        </option>
                      ) : null
                    )
                  : null}
              </select>
            </div>
            <div>
              <button onClick={this.submit}>Submit</button>
            </div>
          </div>
        ) : null}
      </>
    );
  }
}
